<template>
  <div>
    <div class="body">
      <div class="talbar">
        私密空间
        <div class="talbar_icon">
          <i class="iconfont icon-wenben"></i>
          <i class="iconfont icon-tianjia"></i>
        </div>
      </div>
      <div class="content">
        <div class="content_bar">
          <div class="male person" @click="openGrid('male')">
            <img src="../assets/image/male.jpg" alt="" />
          </div>
          <div class="number showtime1">
            在一起
            <div class="number" id="showtime2"></div>
            <div class="number">今日访客 <span>0</span></div>
            <div class="number">访客总量 <span>0</span></div>
          </div>
          <div class="female person" @click="openGrid('female')">
            <img src="../assets/image/female.jpg" alt="" />
          </div>
        </div>
        <div class="content_footer">
          <div class="choose_item">
            <div
              class="item"
              v-for="(item, index) in this.data"
              :key="index"
              @click="clickToRouter(index)"
            >
              <i :class="item.imageName" :data-index="index"></i>
              <span>{{ item.name }}</span>
            </div>
          </div>
          <div class="wenzhang_item">
            <div class="item" v-for="(item,index) in shuoshuo" :key="index">
              <div class="person_information">
                <img :src="item.touxiang" alt="" />
                <div>
                  <span>{{item.name}}</span>
                  <span>{{item.time}}</span>
                </div>
              </div>
              <div class="person_wenzhang">{{item.text}}</div>
              <div class="person_caozuo">
                <i ref="dianzan" class="iconfont icon-zan" @click="dianzan(index)"></i>
                <i class="iconfont icon-duanxin"></i>
                <i class="iconfont icon-zhuanfa"></i>
              </div>
              <div class="person_pinglun">
                <el-input
                  type="text"
                  show-word-limit
                  placeholder="评论"
                  v-model="input"
                  maxlength="200"
                  class="input"
                ></el-input>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="grid" v-show="ifShow">
      <div class="container">
        <span class="goback" @click="clickShow">返回</span>
        <span class="menu-trigger">
            <i class="menu-trigger-bar top"></i>
            <i class="menu-trigger-bar middle"></i>
            <i class="menu-trigger-bar bottom"></i>
        </span>
        <span class="close-trigger">
            <i class="close-trigger-bar left"></i>
            <i class="close-trigger-bar right"></i>
        </span>
        <span class="logo">
            <img :src="person2.touxiang" alt="">
            <span>{{person2.name}}</span>
            <span>{{person2.xingzuo}}</span>
            <span>{{person2.birth}}</span>
            <span>{{person2.shengao}}</span>
            <span>{{person2.tizhong}}</span>
            <span>{{person2.hobby}}</span>
        </span>
        <div class="inner-container">
            <i class="menu-bg top"></i>
            <i class="menu-bg middle"></i>
            <i class="menu-bg bottom"></i>
            <div class="menu-container">
                <ul :style="{backgroundImage:'url('+person2.background+')'}" class="menu">
                    <li>
                        <a href="#">{{person2.xinguzo_jieshao}}</a>
                    </li>
                    <li>
                        <a href="#">{{person2.xingge}}</a>
                    </li>
                    <li>
                        <a href="#">{{person2.match}}</a>
                    </li>
                </ul>
            </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "../css/css1.css"
import {shuoshuo} from "../utils/shuoshuo.js"
import oneday from "../utils/timeCalculator.js";
// import $ from "jquery"
import { data } from "../utils/getItem.js";
import { person } from "../utils/xingzuo.js";
import {TimelineMax,Power2,Power4} from "gsap/dist/gsap"
export default {
  data() {
    return {
      data,
      input: "",
      ifShow:false,
      shuoshuo,
      person,
      person2:{}
    };
  },
  mounted() {
    setInterval(() => {
      oneday();
    }, 1000);
    this.init()
  },
  methods: {
      init(){
           //OPEN TRIGGER
        var openTrigger = document.querySelector(".menu-trigger")
        var openTriggerTop = document.querySelector('.menu-trigger-bar.top');
        var openTriggerMiddle = document.querySelector('.menu-trigger-bar.middle');
        var openTriggerBottom = document.querySelector('.menu-trigger-bar.bottom');

        //CLOSE TRIGGER
        var closeTrigger = document.querySelector(".close-trigger")
        var closeTriggerLeft = document.querySelector('.close-trigger-bar.left');
        var closeTriggerRight = document.querySelector('.close-trigger-bar.right');

        //LOGO
        var logo = document.querySelector(".logo")

        //MENU
        var menuContainer = document.querySelector(".menu-container")
        var menu = document.querySelector(".menu")
        var menuTop = document.querySelector(".menu-bg.top")
        var menuMiddle = document.querySelector(".menu-bg.middle")
        var menuBottom = document.querySelector(".menu-bg.bottom")

        //TL
        var tlOpen = new TimelineMax({ paused: true });
        var tlClose = new TimelineMax({ paused: true });

        //OPEN TIMELINE
        // .add( value:*, position:*, align:String, stagger:Number ) : *
        tlOpen.add("preOpen").
            to(logo, 0.4, {
                scale: 0.8,
                opacity: 0,
                ease: Power2.easeOut
            },
                "preOpen").
            to(openTriggerTop, 0.4, {
                x: "+80px", y: "-80px", delay: 0.1, ease: Power4.easeIn, onComplete: function () {
                    closeTrigger.style.zIndex = 25
                }
            },
                "preOpen").
            to(openTriggerMiddle, 0.4, {
                x: "+=80px", y: "-=80px", ease: Power4.easeIn,
                onComplete: function () {
                    openTrigger.style.visibility = "hidden"
                }
            },
                "preOpen").
            to(openTriggerBottom, 0.4, {
                x: "+=80px", y: "-=80px", delay: 0.2, ease: Power4.easeIn
            },
                "preOpen").
            add("open", "-=0.4").
            to(menuTop, 0.8, {
                y: "13%",
                ease: Power4.easeInOut
            },
                "open").
            to(menuMiddle, 0.8, {
                scaleY: 1,
                ease: Power4.easeInOut
            },
                "open").
            to(menuBottom, 0.8, {
                y: "-114%",
                ease: Power4.easeInOut
            },
                "open").
            fromTo(menu, 0.6, {
                y: 30, opacity: 0, visibility: 'hidden',scale:0
            },
                {
                    y: 0, opacity: 1, visibility: 'visible',scale:1, ease: Power4.easeOut
                },
                "-=0.2").
            add("preClose", "-=0.8").
            to(closeTriggerLeft, 0.8, {
                x: "-=100px", y: "+=100px", ease: Power4.easeOut
            },
                "preClose").
            to(closeTriggerRight, 0.8, {
                x: "+=100px", y: "+=100px", delay: 0.2, ease: Power4.easeOut
            },
                "preClose");

        //CLOSE TIMELINE
        tlClose.add("close").
            to(menuTop, 0.2, {
                backgroundColor: "#6295ca", ease: Power4.easeInOut, onComplete: function () {
                    logo.style.zIndex = 26
                    closeTrigger.style.zIndex = 5
                    openTrigger.style.visibility = "visible"
                }
            },
                "close").
            to(menuMiddle, 0.2, {
                backgroundColor: "#6295ca", ease: Power4.easeInOut
            },
                "close").
            to(menuBottom, 0.2, {
                backgroundColor: "#6295ca", ease: Power4.easeInOut
            },
                "close").
            to(menu, 0.6, {
                y: 20, opacity: 0,scale:0, ease: Power4.easeOut, onComplete: function () {
                    menu.style.visibility ="hidden"
                }
            },
                "close").
            to(logo, 0.8, {
                scale: 1, opacity: 1, ease: Power4.easeInOut
            },
                "close", "+=0.2").
            to(menuTop, 0.8, {
                y: "-113%",
                ease: Power4.easeInOut
            },
                "close", "+=0.2").
            to(menuMiddle, 0.8, {
                scaleY: 0,
                ease: Power4.easeInOut
            },
                "close", "+=0.2").
            to(menuBottom, 0.8, {
                y: "23%",
                ease: Power4.easeInOut,
                onComplete: function () {
                    menuTop.style.backgroundColor = "#fff"
                    menuMiddle.style.backgroundColor = "#ffffff" 
                    menuBottom.style.backgroundColor = "#ffffff" 
                }
            },
                "close", "+=0.2").
            to(closeTriggerLeft, 0.2, {
                x: "+=100px", y: "-=100px", ease: Power4.easeIn
            },
                "close").
            to(closeTriggerRight, 0.2, {
                x: "-=100px", y: "-=100px", delay: 0.1, ease: Power4.easeIn
            },
                "close").
            to(openTriggerTop, 1, {
                x: "-=80px", y: "+=80px", delay: 0.2, ease: Power4.easeOut
            },
                "close").
            to(openTriggerMiddle, 1, {
                x: "-=80px", y: "+=80px", ease: Power4.easeOut
            },
                "close").
            to(openTriggerBottom, 1, {
                x: "-=80px", y: "+=80px", delay: 0.1, ease: Power4.easeOut
            },
                "close");

        //EVENTS
        openTrigger.addEventListener('click', function () {
            if (tlOpen.progress() < 1) {
                tlOpen.play();
            } else {
                tlOpen.restart();
            }
        });

        closeTrigger.addEventListener('click', function () {
            if (tlClose.progress() < 1) {
                tlClose.play();
            } else {
                tlClose.restart();
            }
        });
      },
      openGrid(item){
          if(item === "male"){
              this.ifShow = true
              this.person2 = this.person[0]
          }
          if(item === "female"){
              this.ifShow = true
              this.person2 = this.person[1]
          }
      },
      clickShow(){
          this.ifShow = false
      },
    clickToRouter(index) {
      if (index === 0) {
        this.$router.push("/xiangce");
      }
      if (index === 1) {
        this.$router.push("/shuoshuo");
      }
      if (index === 2) {
        window.location.href = "https://lizhenglong0430.github.io/pza/";
      }
      if (index === 3) {
        window.location.href =
          "https://lizhenglong0430.github.io/ppt/";
      }
      if (index === 4) {
        window.location.href = "http://lizhenglong0430.github.io/music/";
      }
    },
    dianzan(index){
      let ifdianzan = this.$refs.dianzan[index]
      if(ifdianzan.classList.contains("active")){
        ifdianzan.classList.remove("active")
      }else{
        ifdianzan.classList.add("active")
      }
    }
  },
};
</script>
<style scoped>
.body {
  position: relative;
  width: 100%;
  height: 100%;
}
.talbar {
  position: fixed;
  top: 0;
  left: 0;
  height: 5rem;
  width: 100%;
  text-align: center;
  line-height: 5rem;
  font-weight: 600;
  font-size: 18px;
  color: aliceblue;
  border: none;
  outline: none;
  z-index: 999;
  background-image: linear-gradient(
    to right,
    rgb(78, 198, 208),
    rgb(55, 185, 233)
  );
}
.talbar_icon {
  position: absolute;
  display: flex;
  justify-content: space-evenly;
  top: 0;
  right: 0;
  width: 100px;
}
.talbar_icon > i {
  font-weight: 100;
  color: #eee;
  font-size: 25px;
}
.content {
    position: relative;
  height: 100%;
  width: 100%;
  background-color: skyblue;
  background-image: url(../assets/image/fengjing.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-position-x: 30%;
}
.content_bar {
  width: 100%;
  height: 8rem;
  padding-top: 12rem;
  display: flex;
  justify-content: space-around;
}
.content_bar > div {
  position: relative;
  width: 100px;
  height: 100px;
  box-sizing: border-box;
}
.content_bar > div > img {
  width: 95%;
  height: 95%;
  border-radius: 50%;
  border: 2px solid #eee;
}
.showtime1 {
  font-size: 8px;
  text-align: center;
}
#showtime2 {
  color: #eee;
  font-weight: 900px;
  font-size: 13px;
}
.number {
  color: rgb(214, 208, 208);
  margin-top: 3px;
}
.number > span {
  color: rgb(231, 225, 225);
}
.person:nth-child(1)::before {
  content: "小政政";
}
.person:nth-child(3)::before {
  content: "小昀昀";
}
.person::before {
  position: absolute;
  color: rgb(97, 85, 14);
  width: 50%;
  font-weight: 600;
  height: 20%;
  bottom: -15%;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgb(207, 207, 110);
  border-radius: 10px;
  border: 1px solid #eee;
  text-align: center;
}
.content_footer {
  width: 100%;
  height: 100%;
  margin-top: 3rem;
  background-color: #fff;
}
.choose_item {
  position: relative;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 5rem;
  border-bottom: 8px solid rgb(213, 204, 204);
}
.choose_item > .item {
  position: relative;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.choose_item > .item > i {
  font-size: 33px;
  font-weight: 400;
  color: #000;
}
.choose_item > .item:nth-child(2) > i {
  font-weight: 800;
}
.choose_item > .item > span {
  color: #000;
  font-weight: 500;
  text-align: center;
}
.choose_item > .item::before {
  content: "";
  position: absolute;
  top: -5px;
  right: -10px;
  width: 10px;
  height: 10px;
  background-color: red;
  border-radius: 50%;
}
.wenzhang_item {
  width: 100%;
  height: 100%;
}
.wenzhang_item > .item {
  min-height: 15rem;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  position: relative;
  padding-left: 10px;
  border-bottom: 15px solid rgb(213, 204, 204);
}
.wenzhang_item > .item > .person_information {
  min-height: 5rem;
  width: 100%;
  /* background-color: pink; */
  margin-top: 2rem;
  display: flex;
  align-items: center;
}
.person_information > img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}
.person_information > div {
  padding-left: 10px;
  display: flex;
  flex-direction: column;
}
.person_wenzhang {
  width: 100%;
  min-height: 4rem;
  /* background-color: skyblue; */
}
.person_caozuo {
  width: calc(100%);
  text-align: right;
}
.person_caozuo > i {
  font-size: 20px;
  margin-right: 10px;
  font-weight: 600;
}
.person_caozuo > i.active{
  color: red;
}
.person_pinglun {
  width: calc(100% - 10px);
  height: 4rem;
  /* background-color: orangered; */
}
.person_pinglun > .input {
  line-height: 4rem;
}
</style>


