import Vue from 'vue'
import Router from 'vue-router'
import Xiangce from '@/components/Xiangce'
import Shuoshuo from '@/components/Shuoshuo'
import Personal from '@/components/Personal'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:"/",
      redirect:"/home"
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path:"/xiangce",
      name:"Xiangce",
      component:Xiangce
    },
    {
      path:"/shuoshuo",
      name:"Shuoshuo",
      component:Shuoshuo
    },
    {
      path:"/personal",
      name:"Personal",
      component:Personal
    },
  ]
})



