<template>
    <div class="content">
        <div class="talbar">
            <div class="wenzi" @click="routerToHome">返回</div>
            <div class="wenzi">相册</div>
            <div class="wenzi">日历</div>
        </div>
        <div class="image_background">
            <div class="image_title">
                <div>说说和日志</div>
            </div>
            <div class="image_items">
                <div class="item">
                    <div class="image_time">20201/10/23</div>
                    <div class="image">
                        <img src="../assets/photo/1.jpg" alt="">
                    </div>
                </div>
                <div class="item">
                    <div class="image_time">20201/10/23</div>
                    <div class="image">
                        <img src="../assets/photo/2.jpg" alt="">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    methods:{
        routerToHome(){
            this.$router.push("/home")
        }
    }
}
</script>
<style scoped>
.content{
    width: 100%;
    height: 100%;
    background-color: red;
}
.talbar{
    position: relative;
    width: 100%;
    height: 5rem;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 999;
}
.wenzi{
    font-size: 18px;
    font-weight: 700;
    color: #eee;
}
.wenzi:nth-child(1),
.wenzi:nth-child(3){
    margin: 0 10px;
}
.image_background{
    width: 100%;
    height: 100%;
    background-color: skyblue;
    background-image: url(../assets/image/fengjing2.jpg);
    background-attachment: fixed;
    background-size: cover;
}
.image_title{
    width: 100%;
    height: 18rem;
    background-color: purple;
    background-image: url(../assets/image/nidemingzi.jpg);
    background-size: cover;
    background-position-x: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.image_title > div{
    position: relative;
    color: #eee;
    padding: 20px;
    font-size: 30px;
    font-weight: 700;
    border: 2px solid#eee;
    border-radius: 20px;
}
.image_title > div::before,
.image_title > div::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid#eee;
    border-radius: 20px;
}
.image_title > div::before{
    transform: translate(5%,5%);
}
.image_title > div::after{
    transform: translate(3%,-10%);
}
.image_items{
    width: 100%;
    height: 100%;
    position: relative;
}
.image_items > .item{
    width: calc(100% - 60px);
    margin: 30px;
    height: calc(18rem);
    background-color: #eee;
    display: flex;
    flex-direction: column;
    position: relative;
    background-image: url(../assets/image/留言栏.jpg);
    background-repeat: no-repeat;
    background-position-x: 30%;
}
.image_items > .item > .image_time{
    text-align: center;
    font-size: 15px;
    font-weight: 600;
}
.image_items > .item > .image>img{
    position: relative;
    width: 60%;
    max-width: 190px;
    max-height: 135px;
    left: 50%;
    top: 25%;
    transform: translateX(-50%);
}
</style>


